import logo from './logo.svg';
import './App.css';
import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Link
} from "react-router-dom";
import Home from './components/Home';
import Profile from './components/Profile';
import Game from './components/Game';
import Tool from './components/Tool';
import Contact from './components/Contact';
const ROUTER_BASENAME = process.env.NODE_ENV === 'development' ? '/' : '/portfolio';

function App() {

  function Realtime() {
    var relativeValue = document.getElementById("relativeValue");   
    var style = window.getComputedStyle(relativeValue);
    
    var width = style.width;
    if(window.innerWidth <= 760)
    {
      document.getElementById('inv').style.display = 'inline';
      document.getElementById('tes').style.display = 'none';
    }
    else
    {
      document.getElementById('inv').style.display = 'none';
      document.getElementById('tes').style.display = 'inline';
    }
  }

  window.onresize = function () {
    Realtime();
    };

    window.onload = function(){
      Realtime();
      };

  return (
    <body>

    <div id = "relativeValue">
      {/*<<header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />*/}
        <Router basename={ROUTER_BASENAME}>
        <Switch>
        <Route path="/" exact component={Home} />
        <Route path="/profile/" exact component={Profile} />
        <Route path="/game/" exact component={Game} />
        <Route path="/tool/" exact component={Tool} />
        <Route path="/contact/" exact component={Contact} />
        </Switch>
        <ul id = "TopMenu" class="topnav">
          <div id ="inv">

            <li class = "center"><Link to="/"><font class = "home">nkcUgai</font></Link></li>
          </div>       
          <div id ="tes">
            <li class = "home"><Link to="/"><font class = "home">nkcUgai</font></Link></li>

            <li class="right"><Link to="/contact/">CONTACT</Link></li>
            <li class="right"><Link to="/tool/">TOOL</Link></li>
            <li class="right"><Link to="/game/">GAME</Link></li>
            <li class="right"><Link to="/profile/">PROFILE</Link></li>
          </div>
        </ul>

      </Router>
      
      {/*</header>*/}
    </div>
    </body>

  );
}

export default App;
